<template><section><h1>modal</h1>
<h4>弹出框组件</h4>
<template>
    <coding :code="code1" title="基本" content="基本的modal，使用v-model控制状态。">
        <y-modal v-model="testa" center="" @on-ok="$notify('你点击的确认')">
            <template slot="title">asdfasdf</template>
            <template slot="center">asdfadf</template>
        </y-modal>
        <y-button type="primary" @click.native="testa = true">一个基本的弹出框</y-button>
    </coding>
    <coding :code="code2" title="$confirm" content="不带回调。">   
        <y-button type="primary" @click.native="test1">一个基本的弹出框</y-button>
    </coding>
    <coding :code="code3" title="信息提示" content="假如你只需要提示客户信息，那么这样会很好。">
        <y-button type="primary" @click.native="alert('info')">一条信息</y-button>
        <y-button type="primary" @click.native="alert('success')">一条信息</y-button>
        <y-button type="primary" @click.native="alert('error')">一条信息</y-button>
        <y-button type="primary" @click.native="alert('warning')">一条信息</y-button>
    </coding>
    <coding :code="code3" title="自定义位置" content="自定义modal的位置。">
        <y-modal v-model="testb" center="">
            <template slot="title">asdfasdf</template>
            <template slot="center">asdfadf</template>
        </y-modal>
        <y-modal v-model="testc" :top="100">
            <template slot="title">asdfasdf</template>
            <template slot="center">asdfadf</template>
        </y-modal>
        <y-button type="primary" @click.native="testb = true">center</y-button>
        <y-button type="primary" @click.native="testc = true">top：100</y-button>
    </coding>
</template>

<h2>参数设置</h2>
<table>
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
<th>可选值</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td>标题</td>
<td><code>String</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>content</td>
<td>内容</td>
<td><code>String</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>center</td>
<td>居中显示modal</td>
<td><code>Boolean</code></td>
<td><code>false</code></td>
<td>-</td>
</tr>
<tr>
<td>top</td>
<td>modal距离浏览器顶部</td>
<td><code>Number</code></td>
<td><code>40</code></td>
<td>-</td>
</tr>
<tr>
<td>okText</td>
<td>确认按钮文本</td>
<td><code>String</code></td>
<td><code>确认</code></td>
<td>-</td>
</tr>
<tr>
<td>cancelText</td>
<td>取消按钮文本</td>
<td><code>String</code></td>
<td><code>取消</code></td>
<td>-</td>
</tr>
<tr>
<td>onOk</td>
<td>确认按钮回调事件（同@on-ok）</td>
<td><code>Function</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>onCancel</td>
<td>取消按钮回调事件（同@on-cancel)</td>
<td><code>Function</code></td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
<h2>Events</h2>
<table>
<thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-ok</td>
<td>确认按钮回调事件</td>
<td>-</td>
</tr>
<tr>
<td>on-cancel</td>
<td>取消按钮回调事件</td>
<td>-</td>
</tr>
</tbody>
</table>
<h2>slot</h2>
<table>
<thead>
<tr>
<th>slot名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td>标题</td>
</tr>
<tr>
<td>center</td>
<td>内容</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
    data(){
        return {
            code1:
`<y-modal v-model="testa" center @on-ok="$notify('你点击的确认')">
    <template slot="title">asdfasdf</template>
    <template slot="center">asdfadf</template>
</y-modal>
<y-button type="primary" @click.native="testa = true">一个基本的弹出框</y-button>
`,
            code2:
`<y-button type="primary" @click.native="test1">一个基本的弹出框</y-button>

this.$confirm({
    title: "我是一个弹出框",
    content: "hello world!",
    top: 123,
    onOk: () => {
        this.$notify("你点击的确认")
    },
    onCancel: () => {
        this.$notify("你点击的取消")
    }
})
`,
            code3:
`<y-button type="primary" @click.native="alert('info')">一条信息</y-button>
<y-button type="primary" @click.native="alert('success')">一条信息</y-button>
<y-button type="primary" @click.native="alert('error')">一条信息</y-button>
<y-button type="primary" @click.native="alert('warning')">一条信息</y-button>

alert(type){
    this.$modal[type]({
        "title":"我是一个弹出框",
        "content":"我是一个带有回调函数的弹出框",
        center: true
    })
}
`,
            code3:
`<y-modal v-model="testb" center>
    <template slot="title">asdfasdf</template>
    <template slot="center">asdfadf</template>
</y-modal>
<y-modal v-model="testc" :top="100">
    <template slot="title">asdfasdf</template>
    <template slot="center">asdfadf</template>
</y-modal>
<y-button type="primary" @click.native="testb = true">center</y-button>
<y-button type="primary" @click.native="testc = true">top：100</y-button>
`,
            testa: false,
            testb: false,
            testc: false
        }
    },
    methods:{
        test1(){
            this.$confirm({
                title: "我是一个弹出框",
                content: "hello world!",
                top: 123,
                onOk: () => {
                    this.$notify("你点击的确认")
                },
                onCancel: () => {
                    this.$notify("你点击的取消")
                }
            })
        },
        alert(type){
            this.$modal[type]({
                "title":"我是一个弹出框",
                "content":"我是一个带有回调函数的弹出框",
                center: true
            })
        },
    }
}
</script>


